<template>
  <div class="register">
    <HeaderComponent pageName="register"></HeaderComponent>
    <div class="container">
      <h6>注册{{ ["ec-line"].includes(systemName) ? "" : "企业" }}服务商</h6>
      <el-form
        ref="formRef"
        :model="formData"
        :rules="rules"
        v-show="paramsData.step === 1"
      >
        <el-form-item prop="mobile">
          <el-input
            v-model.trim="formData.mobile"
            placeholder="手机号"
          ></el-input>
        </el-form-item>
        <el-form-item prop="imgCode" class="image-verification">
          <el-input
            v-model.trim="formData.imgCode"
            placeholder="请输入右侧验证码"
          ></el-input>
          <div class="img-code" @click="getImgCode">
            <img :src="paramsData.imgCodeUrl" alt="" />
          </div>
        </el-form-item>
        <el-form-item prop="telephoneCode" class="telephone-verification">
          <el-input
            v-model.trim="formData.telephoneCode"
            placeholder="请输入4位验证码"
          ></el-input>
          <div class="telephone-code">
            <span class="count" v-if="!paramsData.telephoneCodeObj.flag"
              >{{ paramsData.telephoneCodeObj.count }}s</span
            >
            <span class="word" v-else @click="getTelephoneCode"
              >获取验证码</span
            >
          </div>
        </el-form-item>
        <el-form-item>
          <div class="submit-button" @click="nextCLick">下一步</div>
        </el-form-item>
      </el-form>
      <el-form
        ref="ruleFormRef"
        :model="ruleFormData"
        :rules="rules"
        v-show="paramsData.step === 2"
      >
        <el-form-item prop="realName">
          <el-input
            v-model.trim="ruleFormData.realName"
            placeholder="用户名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password" class="password-item">
          <el-input
            v-model.trim="ruleFormData.password"
            placeholder="请设置密码"
            :type="paramsData.passwordType"
          ></el-input>
          <svg
            class="icon"
            aria-hidden="true"
            @click="handlePasswordType('passwordType', paramsData)"
          >
            <use
              :xlink:href="
                paramsData.passwordType === 'password'
                  ? '#icon-password-hide'
                  : '#icon-password-show'
              "
            ></use>
          </svg>
        </el-form-item>
        <el-form-item prop="rePassword" class="password-item">
          <el-input
            v-model.trim="ruleFormData.rePassword"
            placeholder="再次输入密码"
            :type="paramsData.rePasswordType"
          ></el-input>
          <svg
            class="icon"
            aria-hidden="true"
            @click="handlePasswordType('rePasswordType', paramsData)"
          >
            <use
              :xlink:href="
                paramsData.rePasswordType === 'password'
                  ? '#icon-password-hide'
                  : '#icon-password-show'
              "
            ></use>
          </svg>
        </el-form-item>
        <el-form-item>
          <div class="submit-button" @click="submit">完成注册</div>
        </el-form-item>
      </el-form>
    </div>
    <div class="copyright">{{ storeState.copyright }}</div>
  </div>
</template>
<script lang="ts" setup>
import { ElMessage } from "element-plus";
import { ref, reactive, onUnmounted } from "vue";

import { testTelephone } from "jz-tool-lib";
import { multipleAwait, systemName } from "@assets/common";
import { useVueRouter, useVueStore } from "@assets/useHook";
import {
  checkoutTelephone,
  validatePassword,
  validateRePassword,
} from "@assets/validatorHook";

import {
  registerApi,
  imgCodeApi,
  bindRelationApi,
  registerTelephoneCodeApi,
  testTelephoneCodeApi,
} from "@api/login-register-api";

import HeaderComponent from "@components/login-regietr/header-component.vue";

defineOptions({
  name: "register",
});
const formRef = ref();
const ruleFormRef = ref();
const { storeState } = useVueStore();
const { routeQuery } = useVueRouter();

const rules = {
  mobile: [
    {
      validator: (rule, value, callback) => {
        checkoutTelephone(rule, value, callback);
      },
      trigger: "blur",
    },
  ],
  imgCode: [
    {
      required: true,
      message: "请输入右侧验证码",
      trigger: "blur",
    },
  ],
  telephoneCode: [
    {
      required: true,
      message: "请输入4位验证码",
      trigger: "blur",
    },
  ],
  realName: [
    {
      required: true,
      message: "请输入用户名",
      trigger: "blur",
    },
  ],
  password: [
    {
      validator: (rule: any, value: any, callback: any) => {
        validatePassword(
          rule,
          value,
          callback,
          ruleFormData,
          ruleFormRef,
          "rePassword"
        );
      },
      trigger: "blur",
    },
  ],
  rePassword: [
    {
      validator: (rule: any, value: any, callback: any) => {
        validateRePassword(rule, value, callback, ruleFormData, "password");
      },
      trigger: "blur",
    },
  ],
};
const paramsData = reactive({
  step: 1,
  telephoneCodeObj: {
    flag: true,
    timer: 0,
    count: 60,
  },
  imgCodeUrl: imgCodeApi(),
  telephoneCodeFlag: false,
  passwordType: "password",
  rePasswordType: "password",
});
const formData = reactive({
  mobile: "",
  imgCode: "",
  telephoneCode: "",
});
const ruleFormData = reactive({
  realName: "",
  password: "",
  rePassword: "",
});
// 获取图片验证码
const getImgCode = () => {
  paramsData.imgCodeUrl = imgCodeApi() + "?t=" + new Date().getTime();
};
// 获取短信验证码
const getTelephoneCode = () => {
  if (paramsData.telephoneCodeFlag) return;
  paramsData.telephoneCodeFlag = true;
  if (!formData.mobile) {
    paramsData.telephoneCodeFlag = false;
    ElMessage.error("请输入手机号");
    return;
  }
  if (!testTelephone(formData.mobile)) {
    paramsData.telephoneCodeFlag = false;
    ElMessage.error("请输入正确的手机号码");
    return;
  }
  if (!formData.imgCode) {
    paramsData.telephoneCodeFlag = false;
    ElMessage.error("请输入图形验证码");
    return;
  }
  const params = {
    mobile: formData.mobile,
    code: formData.imgCode,
  };
  registerTelephoneCodeApi(params)
    .then(() => {
      ElMessage.success("短信发送成功");
      paramsData.telephoneCodeFlag = false;
      telephoneCodeInterval();
    })
    .catch(() => {
      paramsData.telephoneCodeFlag = false;
      paramsData.imgCodeUrl = imgCodeApi() + "?t=" + new Date().getTime();
    });
};
// 短信验证码倒计时
const telephoneCodeInterval = () => {
  (paramsData.telephoneCodeObj.timer as any) = setInterval(() => {
    if (paramsData.telephoneCodeObj.count === 0) {
      clearInterval(paramsData.telephoneCodeObj.timer);
      paramsData.telephoneCodeObj.flag = true;
      paramsData.telephoneCodeObj.count = 60;
    } else {
      paramsData.telephoneCodeObj.flag = false;
      paramsData.telephoneCodeObj.count -= 1;
    }
  }, 1000);
};
// 下一步
const nextCLick = () => {
  formRef.value.validate((valid: boolean) => {
    if (valid) {
      const params = {
        mobile: formData.mobile,
        checkCode: formData.telephoneCode,
      };
      testTelephoneCodeApi(params).then(() => {
        paramsData.step = 2;
      });
    } else {
      return false;
    }
  });
};
// 提交注册信息
const submit = _.throttle(
  () => {
    ruleFormRef.value.validate(async (valid: boolean) => {
      if (valid) {
        const { state: registerState, data: registerData } =
          await multipleAwait(register());
        if (!registerState) return;
        if (routeQuery.value.inviterId) {
          await multipleAwait(
            bindRelation(registerData.profileId, routeQuery.value.inviterId)
          );
        }
      } else {
        return false;
      }
    });
  },
  3000,
  {
    leading: true,
    trailing: false,
  }
);
// 注册账号
const register = () => {
  const params = {
    mobile: formData.mobile,
    realName: ruleFormData.realName,
    password: ruleFormData.password,
    rePassword: ruleFormData.rePassword,
    agencyCode: "",
    parentId: 0,
  };
  return registerApi(params);
};
// 绑定上下级关系
const bindRelation = (profileId: number, inviterId: string) => {
  const params = {
    mobilePhone: formData.mobile,
    userName: ruleFormData.realName,
    userId: profileId,
    refererId: inviterId,
  };
  return bindRelationApi(params);
};
// 切换密码框type
const handlePasswordType = (type, data) => {
  data[type] = data[type] === "text" ? "password" : "text";
};
onUnmounted(() => {
  clearInterval(paramsData.telephoneCodeObj.timer);
});
</script>
<style lang="scss" scoped>
.register {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  min-height: 100%;
  background: #f5f7fb;

  .container {
    width: 520px;
    padding: 48px 48px 40px;
    z-index: 1;
    background: #ffffff;
    box-shadow: 0 10px 50px 0 rgba(100, 107, 122, 0.1);
    border-radius: 8px;

    h6 {
      font-size: 28px;
      font-weight: 600;
      color: #242934;
      line-height: 28px;
      text-align: center;
      margin-bottom: 58px;
    }

    :deep(.el-form) {
      .el-form-item {
        margin-bottom: 32px;

        .el-input__wrapper {
          height: 58px;
          padding: 15px 24px;
          border-radius: 4px;
          background: #f5f7fb;
          box-shadow: none;

          &.is-focus {
            border: 1px solid #0056ff;
          }
        }

        .el-input__inner {
          font-size: 20px;
          font-weight: 600;
          color: #242934;

          &::placeholder {
            font-size: 16px;
            font-weight: 400;
            color: #9ca6b9;
          }
        }

        &.is-error {
          .el-input__wrapper {
            background: rgba(241, 37, 37, 0.05);

            &::placeholder {
              color: #f12525;
            }
          }
        }
      }

      .password-item {
        .el-form-item__content {
          position: relative;
        }

        .el-input__wrapper {
          padding-right: 56px;
        }

        .icon {
          position: absolute;
          top: 17px;
          right: 16px;
          width: 24px;
          height: 24px;
          cursor: pointer;
        }

        .prompt {
          height: 32px;
          margin-top: 10px;
          line-height: 16px;
          color: #f12525;
        }
      }

      .image-verification {
        position: relative;

        .el-input__inner {
          padding-right: 130px;
        }

        .img-code {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          top: 11px;
          right: 20px;
          width: 90px;
          height: 36px;
          cursor: pointer;

          img {
            width: 100%;
            height: 100%;
          }
        }
      }

      .telephone-verification {
        position: relative;

        .el-input__inner {
          padding-right: 130px;
        }

        .telephone-code {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          top: 11px;
          right: 20px;
          width: 90px;
          height: 36px;
          font-size: 16px;
          color: #0056ff;
          cursor: pointer;

          span {
            height: 100%;
          }
        }
      }

      .reading-agreement {
        margin: 28px 0 48px;

        .el-form-item__content {
          display: flex;
          align-items: center;
          font-size: 14px;
          font-weight: 400;
          color: #9ca6b9;
          line-height: 14px;

          p {
            margin-left: 5px;

            span {
              color: #646b7a;
              margin: 0 5px;
            }
          }

          .el-checkbox {
            height: 16px;

            .el-checkbox__input {
              .el-checkbox__inner {
                width: 16px;
                height: 16px;

                &::after {
                  height: 9px;
                  left: 5px;
                }
              }

              &.is-checked {
                + .el-checkbox__label {
                  color: #0056ff;
                }

                .el-checkbox__inner {
                  background-color: #0056ff;
                  border-color: #0056ff;
                }
              }

              &.is-focus .el-checkbox__inner {
                border-color: #deedfc;
                width: 16px;
                height: 16px;
              }
            }
          }
        }

        .forget-password {
          cursor: pointer;
        }
      }

      .submit-button {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 60px;
        cursor: pointer;
        font-size: 18px;
        font-weight: 500;
        color: #ffffff;
        line-height: 25px;
        background: #0056ff;
        border-radius: 4px;
      }
    }
  }

  .copyright {
    position: absolute;
    bottom: 80px;
    width: 100%;
    font-size: 14px;
    font-weight: 400;
    color: #9ca6b9;
    line-height: 14px;
    text-align: center;
  }
}
</style>
